@use '../styles.scss' as *;

.skills{
  max-width: 1200px;
  margin: auto;
  padding: 100px 1rem;

  h2{
    font-family: 'pretendard_b';
    font-size: 5rem;
    margin: 40px 0;

    span {
      color: $point;
    }
  }

  .skills_icon{
    width: 100%;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    text-align: center;
    
    .icons{
      display: flex;
      justify-content: center;
      gap: 20px; 
      flex-wrap: wrap;

      .icon{
        width: 50%;
        display: flex;
  
        img{
          width: 60px;
        }
      }
    }

    .coding,
    .design,
    .itq{
      justify-self: center;

      h3{
        font-size: 2.5rem;
        padding-bottom: 30px;
      }

      .text{
        text-align: start;
        align-content: center;
        padding-left: 10px;
      }
    }
  }
}
